// import OTP from "../Otp";

const numSchema = object({
  mobileNo: number()
    .required("Phone number is required")
    .positive()
    .integer("must be numeric"),
  // .integer("Input should consist 0-9 only"),
});

const pageMode = {
  NumberPage: "NUMBER PAGE",

  SuccessPage: "SUCCESS PAGE",
};

